pre {
  margin: 0;
}

input,
textarea {
  outline: none;
}

.container {
  max-width: 824px;
}

.form {
  &__textarea-block {
    width: 100%;
    display: flex;
    justify-content: space-between;
  }

  &__textarea {
    height: 210px;
    width: 100%;
    margin: 0 5px 5px 0;
    resize: none;
  }
}

.input {
  &__box {
    display: flex;
    justify-content: space-around;
    min-height: 230px;
  }

  &__block {
    max-width: 412px;
    width: 100%;
    margin: 5px 10px 5px 0;
  }
  &__text {
    width: 100%;
    display: block;
    margin-bottom: 5px;
  }
}

.btn {
  margin: 34px 0 0 0;
  display: block;
  width: 100%;
  cursor: pointer;
}

.info {
  &__box {
    display: flex;
    justify-content: space-around;
    border-top: 1px dashed #ccc;
    margin: 10px 0;
    padding: 10px 0;
  }
  &__block {
    max-width: 412px;
    width: 100%;
    margin: 5px 10px 5px 0;
  }
}

.footer {
  width: 100%;
  display: flex;
  justify-content: space-between;
  textarea {
    height: 600px;
    width: 100%;
    margin: 0 5px 5px 0;
  }
}

