.checkbox-dlc {
  &__list {
    margin-top: 10px;
    label {
      cursor: pointer;
      display: block;
    }
  }
}
.check {
  opacity: 0;
  &__label {
    width: 90px;
    height: 30px;
    background-color: #efefef;
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-radius: 5px;
    padding: 0 10px;
    box-sizing: border-box;
    position: relative;
    cursor: pointer;
    transition: all 1s;
    &::after {
      content: '';
      display: block;
      position: absolute;
      left: 0;
      border-radius: 5px;
      width: 45px;
      height: 30px;
      background-color: #404040;
      transition: all .5s;
    }
  }
  &__text {
    font-family: sans-serif;
    font-size: 16px;
    font-weight: 300;
    text-transform: uppercase;
    user-select: none;
  }
  &:checked + .check {
    &__label {
      &::after {
        left: 100%;
        transform: translateX(-100%);
      }
    }
  }
}

